@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');
.Container{
    height:75vh;
    width:80%;
    margin: auto;
    background-color: rgba(0, 0, 0, 0.3);
    overflow-y: auto;
    border-radius: 20px;
    box-shadow: 0px 0px 50px #000;
    color: #61dafb;
    overflow-y: auto;
}
.Container::-webkit-scrollbar {
    width: 0;
  }
.Welcome-Text{
    color: #61dafb;
    font-family: 'Lobster', cursive;
    font-size: 50px;
    text-shadow: 0px 0px 10px #000;
}
.QuestionInfo {
    display: flex;
    justify-content: space-around;
}
.Question-Container{
    
}
.OptionList{
    display: flex;
    flex-direction: column;
    width:100%;
    align-items: center;
}
.OptionList div{
    display: flex;
    flex-direction: column;
}
.OptionList button{
    margin: 5px;
    cursor: pointer;
    width: auto;
    font-weight: 800;
    height: 40px;
    background-color:rgba(255, 255, 255,0.1);
    color:#61dafb;
    border-radius: 10px;
    border:  1px solid rgb(132, 216, 219);
    padding: 5px 10px;
}
.OptionList button:hover{
    background-color:transparent;
}
.NextQuestionDiv{
 display: flex;
 justify-content: space-around;
}
.NextQuestionDiv button{
    margin: 5px;
    cursor: pointer;
    width: auto;
    font-weight: 800;
    height: 40px;
    background-color:rgba(255, 255, 255,0.1);
    color:#61dafb;
    border-radius: 10px;
    border:  1px solid rgb(132, 216, 219);
    padding: 5px 10px;
}
.Loader{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}