@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-image: linear-gradient(to top left, blue,red);
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.Home{  
    color: #61dafb;
    text-shadow: 0px 0px 5px #1bb2db;
}
.startbtn{
    margin: 5px;
    cursor: pointer;
    width: auto;
    font-weight: 800;
    font-size: 20px;
    height: 40px;
    background-color:rgba(0, 0, 0,0.2);
    color:#61dafb;
    border-radius: 10px;
    border:  1px solid rgb(132, 216, 219);
    padding: 5px 10px; 
}

.startbtn:hover{
    background-color:transparent;
}
.ContainerSubjects{
        height:70vh;
        width:80%;
        margin: auto;
        background-color: rgba(0, 0, 0, 0.3);
        overflow-y: auto;
        border-radius: 20px;
        box-shadow: 0px 0px 50px #000;
        color: #61dafb;
        overflow-y: auto;
        display: flex;
        justify-content: space-around;
        align-items: center;
}
.Container{
    height:75vh;
    width:80%;
    margin: auto;
    background-color: rgba(0, 0, 0, 0.3);
    overflow-y: auto;
    border-radius: 20px;
    box-shadow: 0px 0px 50px #000;
    color: #61dafb;
    overflow-y: auto;
}
.Container::-webkit-scrollbar {
    width: 0;
  }
.Welcome-Text{
    color: #61dafb;
    font-family: 'Lobster', cursive;
    font-size: 50px;
    text-shadow: 0px 0px 10px #000;
}
.QuestionInfo {
    display: flex;
    justify-content: space-around;
}
.Question-Container{
    
}
.OptionList{
    display: flex;
    flex-direction: column;
    width:100%;
    align-items: center;
}
.OptionList div{
    display: flex;
    flex-direction: column;
}
.OptionList button{
    margin: 5px;
    cursor: pointer;
    width: auto;
    font-weight: 800;
    height: 40px;
    background-color:rgba(255, 255, 255,0.1);
    color:#61dafb;
    border-radius: 10px;
    border:  1px solid rgb(132, 216, 219);
    padding: 5px 10px;
}
.OptionList button:hover{
    background-color:transparent;
}
.NextQuestionDiv{
 display: flex;
 justify-content: space-around;
}
.NextQuestionDiv button{
    margin: 5px;
    cursor: pointer;
    width: auto;
    font-weight: 800;
    height: 40px;
    background-color:rgba(255, 255, 255,0.1);
    color:#61dafb;
    border-radius: 10px;
    border:  1px solid rgb(132, 216, 219);
    padding: 5px 10px;
}
.Loader{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
